import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useEducationTypes() {
  const educationTypes = ref([]);
  const fetchEducationTypes = async () => {
    const { data } = await InfoService.fetchEducationTypes();
    educationTypes.value = data;
  };
  onMounted(async () => {
    await fetchEducationTypes();
  });
  return {
    educationTypes,
  };
}
