<template>
  <section class="bg-white p-3 rounded-md">
    <section>
      <PageTitle> ITMni tamomlagan bolalarning bandligini ta'minlash </PageTitle>
      <TableTeenagersForSelectWrapper v-model:selected-juvenile="selectedJuvenile" :update-juveniles-list="updateJuvenilesList" :fetch-juveniles-list-handler="fetchEmployedJuvenilesList" />
      <span class="error_message">{{ selectedJuvenileError }}</span>
    </section>
    <section v-if="selectedJuvenile" class="my-4">
      <h1 class="text-2xl my-4">Tanlangan bolaning shaxsiy ma'lumotlari</h1>
      <div class="flex">
        <PassportCard :passport-info="selectedJuvenile" @canceled="setSelectedJuvenile(null)" />
      </div>
    </section>
    <section v-if="selectedJuvenile">
      <EmploymentForm :clear-form-mode="clearForm" @submit="handleSubmit" />
    </section>
  </section>
</template>

<script setup>
import TableTeenagersForSelectWrapper from '../components/Tables/TableTeenagersForSelectWrapper';
import PassportCard from '../components/PassportCard';
import {ref, watch} from 'vue';
import useAlert from '../composables/useAlert';
import EmploymentForm from '../components/EmploymentForm';
import PageTitle from '../components/LayoutItems/PageTitle';
import AllocateService from '../services/allocate.service';
import EmploymentService from '../services/employment.service';
import useClearForm from '../composables/useClearForm';
import useErrorAlert from '../composables/useErrorAlert';

const selectedJuvenile = ref(null);
const selectedJuvenileError = ref('');
const {clearForm, setClearForm} = useClearForm();
const {fetchEmployedJuvenilesList} = AllocateService;
const {alertIt} = useAlert();
const {alertThisError} = useErrorAlert();
const {addJuvenileToEmployment} = EmploymentService;

const updateJuvenilesList = ref(false);
const refreshJuvenileList = () => {
  updateJuvenilesList.value = true;
};

watch(
  () => selectedJuvenile.value,
  (val) => {
    if (val && selectedJuvenileError.value) {
      selectedJuvenileError.value = '';
    }
  }
);

function clearAllFields() {
  selectedJuvenile.value = null;
  setClearForm(true);
}

function setSelectedJuvenile(juvenile) {
  selectedJuvenile.value = juvenile;
}

async function handleSubmit(formData) {
  try {
    if (!selectedJuvenile.value) {
      selectedJuvenileError.value = 'Bola tanlanishi shart!';
      return null;
    }
    const res = await addJuvenileToEmployment(formData, selectedJuvenile.value.id);
    console.log(res);
    clearAllFields();
    refreshJuvenileList();
    alertIt(res.data.message, 'success');
  } catch (e) {
    alertThisError(e);
  }
}
</script>

<style scoped></style>
