      <div v-if="is_applied_document === false" class="form_wrapper my-3">
        <label class="input_label" for="not_applied_reason">Topshirmaganlik sababi </label>
        <input v-validate-cyrillic id="not_applied_reason" v-model="not_applied_reason" placeholder="Topshirmaganlik sababini kiriting" class="base_input" name="not_applied_reason" type="text" />
        <span class="error_message">{{ not_applied_reasonError }}</span>
      </div>
    </div>
    <div class="grid grid-cols-2 xl:grid-cols-3 gap-4 2xl:w-[80%]">
      <div v-if="is_applied_document" class="form_wrapper my-3">
        <label class="input_label" for="employment_education_type"> Ta'lim muassasa turi </label>
        <SelectBox id="employment_education_type" v-model="employment_education_type" :options="educationTypes" show-by="text" track-by="id" placeholder="Ta'lim muassasa turini tanlang" />
        <span class="error_message">{{ employment_education_typeError }}</span>
      </div>
      <div v-if="is_applied_document" class="form_wrapper my-3">
        <label class="input_label" for="school_name">Ta'lim muassasa nomi</label>
        <input v-validate-cyrillic id="school_name" v-model="school_name" placeholder="Ta'lim muassasa nomini kiriting" class="base_input" name="school_name" type="text" />
        <span class="error_message">{{ school_nameError }}</span>
      </div>
      <div v-if="is_applied_document" class="form_wrapper my-3">
        <label class="input_label" for="education_direction">Ta'lim yo'nalishi</label>
        <input v-validate-cyrillic id="education_direction" v-model="education_direction" placeholder="Ta'lim yo'nalishini kiriting" class="base_input" name="education_direction" type="text" />
        <span class="error_message">{{ education_directionError }}</span>
      </div>
      <div v-if="is_applied_document" class="form_wrapper my-3">
        <label class="input_label" for="employment_speciality">Mutaxassisligi</label>
        <input v-validate-cyrillic id="employment_speciality" v-model="employment_speciality" placeholder="Mutaxassisligini kiriting" class="base_input" name="employment_speciality" type="text" />
        <span class="error_message">{{ employment_specialityError }}</span>
      </div>
      <div v-if="is_applied_document" class="form_wrapper my-3">
        <label class="input_label" for="is_accepted_to_school"> Talim muassasasiga qabul qilinganmi </label>
        <ButtonGroup id="is_accepted_to_school" v-model:selected-item="is_accepted_to_school" :titles="btnGroupYesNo" />
        <span class="error_message">{{ is_accepted_to_schoolError }}</span>
      </div>
      <div v-if="is_applied_document && is_accepted_to_school" class="form_wrapper my-3">
        <label class="input_label" for="accepted_school">Qabul qilingan talim muassasasi </label>
        <input v-validate-cyrillic id="accepted_school" v-model="accepted_school" placeholder="Talim muassasasini kiriting" class="base_input" name="accepted_school" type="text" />
        <span class="error_message">{{ accepted_schoolError }}</span>
      </div>
