        <FileInput :id="'school_applied_file2'" v-model:files="military_conscripted_file" :multiple-files="false">
          <template #icon>
            <div class="flex justify-between shadow rounded w-full">
              <p class="flex items-center text-gray-400 px-3 overflow-hidden overflow-ellipsis base_input border-none">Asosni yuklang</p>
              <span class="flex px-4 items-center justify-center rounded bg-primary text-white tracking-wide text-sm cursor-pointer"> YUKLASH </span>
            </div>
          </template>
        </FileInput>
