      <div class="form_wrapper my-3">
        <label class="input_label" for="employment"> Bandligi taminlanganlinganmi </label>
        <ButtonGroup id="employment" v-model:selected-item="employment" :titles="btnGroupYesNo" />
        <span class="error_message">{{ employmentError }}</span>
      </div>
      <div v-if="employment" class="form_wrapper my-3">
        <label id="employment_file" class="input_label"> Asos </label>
        <FileInput :id="'employment_file3'" v-model:files="employment_file" :multiple-files="false">
          <template #icon>
            <div class="flex justify-between shadow rounded w-full">
              <p class="flex items-center text-gray-400 px-3 overflow-hidden overflow-ellipsis base_input border-none">Asosni yuklang</p>
              <span class="flex px-4 items-center justify-center rounded bg-primary text-white tracking-wide text-sm cursor-pointer"> YUKLASH </span>
            </div>
          </template>
        </FileInput>
        <span class="error_message">{{ employment_fileError }}</span>
      </div>
