        <label class="input_label" for="neighborhood_coach">Mahalla va oilani qo'llab quvvatlash bo'limi tamonidan biriktirilgan murabbiy FISH</label>
        <textarea id="neighborhood_coach" v-model="neighborhood_coach" placeholder="FISH ni kiriting" class="base_input" name="neighborhood_coach"></textarea>
        <span class="error_message">{{ neighborhood_coachError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="employment_inspector">Profilaktika inspektori FISH</label>
        <textarea id="employment_inspector" v-model="employment_inspector" placeholder="FISH ni kiriting" class="base_input" name="employment_inspector" type="text"></textarea>
        <span class="error_message">{{ employment_inspectorError }}</span>
      </div>
    </div>
    <button v-if="isEditMode" class="px-3 m-2 py-2 border bg-primary text-white rounded-md" type="submit" :disabled="isSubmitting">Tahrirlashni tugatish</button>
    <button v-else class="px-3 m-2 py-2 border bg-primary text-white rounded-md" type="submit" :disabled="isSubmitting">Saqlash</button>
  </form>
</template>

<script setup>
import {Icon} from '@iconify/vue';
import SelectBox from './Common/SelectBox';
import {useField, useForm} from 'vee-validate';
import * as yup from 'yup';
import {defineEmits, defineProps, toRefs, watch} from 'vue';
import ButtonGroup from './Common/ButtonGroup';
import {btnGroupYesNo} from '../utils/buttonGroupTitles';
import useEducationTypes from '../composables/selectbox-list/useEducationTypes';
import FileInput from './Common/InputFile/FileInput';

const {educationTypes} = useEducationTypes();
const emit = defineEmits(['submit']);
const props = defineProps({
  initialFields: {
    type: Object,
    default: function () {
      return null;
    },
  },
  isEditMode: {
    type: Boolean,
    default: false,
  },
  clearFormMode: {
    type: Boolean,
    default: false,
  },
});

const {handleSubmit, isSubmitting, resetForm} = useForm();

const {clearFormMode} = toRefs(props);
watch(
  () => clearFormMode.value,
  (clearFormMode) => {
    if (clearFormMode === true) {
      resetForm();
    }
  }
);

function onInvalidSubmit({values, errors, results}) {
  console.log(Object.keys(errors)[0]);
}

const onSubmit = handleSubmit(async (values) => {
  const formData = new FormData();
  Object.entries(values).map(([key, val]) => {
    if (val) {
      formData.append(key, val);
    }
  });
  emit('submit', formData);
}, onInvalidSubmit);

const {value: education_direction, errorMessage: education_directionError} = useField('education_direction', yup.string(), {
  initialValue: '',
});
const {value: school_name, errorMessage: school_nameError} = useField('school_name', yup.string(), {
  initialValue: '',
});

const {value: employment_education_type, errorMessage: employment_education_typeError} = useField('employment_education_type', yup.mixed(), {
  initialValue: null,
});
const {value: accepted_school, errorMessage: accepted_schoolError} = useField('accepted_school', yup.string(), {
  initialValue: '',
});

const {value: not_applied_reason, errorMessage: not_applied_reasonError} = useField('not_applied_reason', yup.string(), {
  initialValue: '',
});

const {value: school_applied_file, errorMessage: school_applied_fileError} = useField('school_applied_file', yup.mixed());

const {value: military_conscripted_file, errorMessage: military_conscripted_fileError} = useField('military_conscripted_file', yup.mixed());

const {value: employment_file, errorMessage: employment_fileError} = useField('employment_file', yup.mixed());

const {value: employment, errorMessage: employmentError} = useField('employment', yup.boolean());

const {value: employment_speciality, errorMessage: employment_specialityError} = useField('employment_speciality', yup.string(), {
  initialValue: '',
});
const {value: neighborhood_coach, errorMessage: neighborhood_coachError} = useField('neighborhood_coach', yup.string(), {
  initialValue: '',
});
const {value: employment_inspector, errorMessage: employment_inspectorError} = useField('employment_inspector', yup.string(), {
  initialValue: '',
});
const {value: is_applied_document, errorMessage: is_applied_documentError} = useField('is_applied_document', yup.boolean());
const {value: is_accepted_to_school, errorMessage: is_accepted_to_schoolError} = useField('is_accepted_to_school', yup.boolean());
const {value: is_military, errorMessage: is_militaryError} = useField('is_military', yup.boolean());

// const dynamicValidationForHaveBeenInItm = computed(()=>{
//   if (is_in_itm.value){
//     return  yup.number()
//   } else {
//     return yup.number().notRequired()
//   }
// })
</script>

